<script>

import { mapState } from 'vuex';
import { login } from '@/utils/public';
import { DApp } from '@/utils/wallet';
import { accDiv } from '@/utils/public';
import moment from 'moment';
import NoSleep from 'nosleep.js';

export default {
	name: 'Town',

	components: {
	},

  data() {
    return {
      logined: false,
      tipMsg: '',
      checked: true,
      land: [],
      gemfactory: [],
      feedmill: [],
      bakery: [],
      cow: [],
      sheep: [],
      chicken: [],
      wheat: 0,
      corn: 0,
      carrot: 0,
      ruby: 0,
      beryl: 0,
      topaz: 0,
      cowfood: 0,
      chkfood: 0,
      shpfood: 0,
      milk: 0,
      egg: 0,
      wool: 0,
      bread: 0,
      cookie: 0,
      cake: 0,
      dcash: 0,
      orderLogs: [],
      works: '',
      price: [],
      delay: 5000,
      updateDelay: 5000,
      failDelay: 5000,
      failMax: 5,
      freeCPU: true,
      logMsg: [],
      showProduct: false,
      showCalc: false,
      requireAsset: '',
      createAsset: '',
      requireTime: '',
      endTime: ''
    }
  },
  computed: {
    ...mapState({
      account: state => state.app.account,
    }),
  },
  methods: {
    handleLogin() {
      login(this, () => {
        this.logined = true;
        //this.account.name = "dfsdeveloper"; //just for test
        this.handleUpdateTip();
        this.handleReset();
        this.handleGetAccNft();
        this.handleGetWorks();
        this.handleGetAccAssets();
        this.handleGetOrderLogs();
      })
    },
    handleReset() {
      this.land = [];
      this.gemfactory = [];
      this.feedmill = [];
      this.bakery = [];
      this.cow = [];
      this.sheep = [];
      this.chicken = [];
      this.wheat = 0;
      this.corn = 0;
      this.carrot = 0;
      this.ruby = 0;
      this.beryl = 0;
      this.topaz = 0;
      this.cowfood = 0;
      this.chkfood = 0;
      this.shpfood = 0;
      this.milk = 0;
      this.egg = 0;
      this.wool = 0;
      this.bread = 0;
      this.cookie = 0;
      this.cake = 0;
      this.dcash = 0;
    },
    handleNoPasswordTx() {
      if (typeof this.account.name === 'undefined') {
        this.$toast({type: 'fail', message: "未登陆",})
        return;
      } 
      this.$dialog.confirm({
        title: '免密交易',
        message: '钱包登陆应用后的首次交易都需要输入密码或指纹，之后的交易便可免密执行，所以在进行自动化生产之前，必须先完成一次交易，后续的交易才能自动免密执行。按“确认”按钮后，会执行一个简单的转账操作，来作为本应用的首次交易。注意：自动化生产目前只支持Start钱包，其它功能不影响。',
      }).then(() => {
        const name = this.account.name;
        const permission = this.account.permissions;
        const params = {
          actions: [{
            account: 'dfsfarmitems',
            name: 'transfer',
            authorization: [{
              actor: name,
              permission,
            }],
            data: {
              from: name,
              to: 'davidaccount',
              quantity: '0.0001 DCASH',
              memo: 'tip',
            }          
          }]
        }
        DApp.toTransaction(params, (err) => {
          err = this.handleDealWithError(err)
          if (err && err.code == 402) {
            this.$toast({
              type: 'fail',
              message: err.message,
            })
            this.logMsg.push("handleNoPasswordTx tx error");
            this.logMsg.push(err.message);
            return;
          }
          if (err) {
            this.$toast({
              type: 'fail',
              message: err.message,
            })
            this.logMsg.push("handleNoPasswordTx tx error");
            this.logMsg.push(err.message);
            return;
          }
          this.$toast({
            type: 'success',
            message: 'Success'
          })
        })  
      });     
    },
    handleUpdateTip() {
      if (typeof this.account.name !== 'undefined') {
        this.tipMsg = '账号已登陆'
        this.logined = true;
      } else {
        this.tipMsg = '请登陆账号'
        this.logined = false;
      }
    },
    handleDealWithError(err) {
      //非免费CPU
      // {
      //     "code": "402",
      //     "message": "User rejected the signature request"
      // } 
      // {
      //     "code": 3050003,
      //     "message": "assertion failure with message: no balance object found"
      // }     
      //免费CPU
      // {
      //     "code": 999,
      //     "message": "{\"type\":\"signature_rejected\",\"message\":\"User rejected the signature request\",\"code\":402,\"isError\":true}"
      // }
      // {
      //     "code": 500,
      //     "msg": "{\"code\":500,\"message\":\"Internal Service Error\",\"error\":{\"code\":3050003,\"name\":\"eosio_assert_message_exception\",\"what\":\"eosio_assert_message assertion failure\",\"details\":[{\"message\":\"assertion failure with message: no balance object found\",\"file\":\"cf_system.cpp\",\"line_number\":14,\"method\":\"eosio_assert\"}]}}"
      // }
      if (err.message !== undefined) {
        if (this.isJSON(err.message)) {
          const content = JSON.parse(err.message)
          const code = content.code;
          const message = content.message;
          return {code, message}
        } else {
          return err;
        }
      } else if (err.msg !== undefined) {
        const content = JSON.parse(err.msg)
        const code = content.error.code;
        const message = content.error.details[0].message;
        return {code, message}
      } else {
        return err;
      }
    },
    isJSON(str) {
      if (typeof str == 'string') {
        try {
          var obj=JSON.parse(str);
          if(typeof obj == 'object' && obj ){
              return true;
          }else{
              return false;
          }
        } catch(e) {
          //console.log('error：'+str+'!!!'+e);
          return false;
        }
      }
    },
    async handleGetAccAssets() {
      const params = {
        "code":"dfsfarmitems",
        "scope":this.account.name,
        "table":"accounts",
        //"lower_bound": "1000",
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: "获取账号资产失败",})
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        return
      }
      rows.forEach(elem => {
        const assets = elem.balance.split(' ');

