    async handleGetTask(nft) {
      nft.forEach(async elem => {
        const params = {
          "code":"dfsmetaverse",
          "scope":"dfsmetaverse",
          "table":"tasks",
          "lower_bound": elem.id,
          "upper_bound": elem.id,
          "json":true,
        }
        const {status, result} = await this.$api.get_table_rows(params)
        if (!status) {
          this.$toast({type: 'fail', message: "获取任务失败"})
          this.logMsg.push("获取任务失败");
          return
        }
        const rows = result.rows || []
        if (!rows.length) {
          return
        }
        elem.task = rows[0];
        elem.task.create_time = moment(`${elem.task.create_time}.000+0000`).format();
        const mature_time = moment(`${elem.task.mature_time}.000+0000`);
        elem.task.mature_time = mature_time.format();
        elem.task.end_time = mature_time.format('YYYY-MM-DD HH:mm:ss');
        const countDown = mature_time.diff(moment());
        if (countDown <= 0) {
          elem.task.countDown = 0;
        } else {
          elem.task.countDown = countDown;
        }
        elem.task.exist = true;
      })
    },
    async handleUpdateTask(elem) {
      const params = {
        "code":"dfsmetaverse",
        "scope":"dfsmetaverse",
        "table":"tasks",
        "lower_bound": elem.id,
        "upper_bound": elem.id,
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: "获取任务失败"})
        this.logMsg.push("获取任务失败");
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        elem.task = {countDown: 0, exist: false};
        return
      }
      elem.task = rows[0];
      elem.task.create_time = moment(`${elem.task.create_time}.000+0000`).format();
      const mature_time = moment(`${elem.task.mature_time}.000+0000`);
      elem.task.mature_time = mature_time.format();
      elem.task.end_time = mature_time.format('YYYY-MM-DD HH:mm:ss');
      const countDown = mature_time.diff(moment());
      if (countDown <= 0) {
        elem.task.countDown = 0;
      } else {
        elem.task.countDown = countDown;
      }
      elem.task.exist = true;
    },
    handleGetAllTask() {
      this.handleGetTask(this.land);
      this.handleGetTask(this.gemfactory);
      this.handleGetTask(this.feedmill);
      this.handleGetTask(this.bakery);
      this.handleGetTask(this.cow);
      this.handleGetTask(this.sheep);
      this.handleGetTask(this.chicken);
    },
    handleHarvest(v) {
      if (v.task.exist === false) {
        this.$toast({type: 'fail', message: "不存在任务"})
        this.logMsg.push("不存在任务");
        return;        
      }
      if (moment(v.task.mature_time).diff(moment()) > 0) {
        this.$toast({type: 'fail', message: "未到收取时间"})  
        this.logMsg.push("未到收取时间");
        return; 
      }
      const name = this.account.name;
      const permission = this.account.permissions;
      const params = {
        actions: [{
          account: 'dfsmetaverse',
          name: 'harvest',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            taskid: v.id,
          }
        }]
      }
      DApp.toTransaction(params, (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleHarvest tx error");
          this.logMsg.push(err.message);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleHarvest tx error");
          this.logMsg.push(err.message);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        if (!v.autoSwitch) {
          setTimeout(this.handleUpdateTask, this.updateDelay, v);
        }
      })
    },
    handleCalc(v) {
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "请选择生产物品"})
        this.logMsg.push("请选择生产物品");
        return;        
      }
      if (this.works[workid-1].id != workid) {
        this.$toast({type: 'fail', message: "工作id错误"})
        this.logMsg.push("工作id错误");
        return;             
      }

      let requireAsset = this.works[workid-1].require_asset.slice(); //需要复制数组，否则会影响原数据
      requireAsset.forEach((elem, i) => {
        let asset = elem.split(" ");
        asset[0] = (parseInt(asset[0]) * multiple * round).toFixed(4);
        requireAsset[i] = asset[0] + ' ' + asset[1]
      })
      let createAsset = this.works[workid-1].create_asset.slice(); //需要复制数组，否则会影响原数据
      let asset = createAsset.split(" ");
      asset[0] = (parseInt(asset[0]) * multiple * round).toFixed(4);
      createAsset = asset[0] + ' ' + asset[1]

      let requireTime = this.works[workid-1].require_time * multiple * round;
      this.requireAsset = requireAsset;
      this.createAsset = createAsset;
      this.requireTime = requireTime / 60;
      this.endTime = moment().add(this.requireTime, 'm').format('YYYY-MM-DD HH:mm:ss')
      this.showCalc = true;
    },
    handleGemCalc(v) {
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "请选择生产物品",})
        this.logMsg.push("请选择生产物品");
        return;        
      }
      let quantity = '';
      let gem = '';
      if (workid === "RUBY") {
        quantity = accDiv(multiple*round, this.price[0]).toFixed(4) + ' ' + 'USDC'
        gem = multiple.toFixed(4) + ' ' + workid;
      } else if (workid === "BERYL") {
        quantity = accDiv(multiple*round, this.price[1]).toFixed(4) + ' ' + 'DFG'
        gem = multiple.toFixed(4) + ' ' + workid
      } else if (workid === "TOPAZ") {
        quantity = accDiv(multiple*round, this.price[2]).toFixed(8) + ' ' + 'TAG'
        gem = multiple.toFixed(4) + ' ' + workid
      }
      this.requireAsset = quantity;
      this.createAsset = gem;
      this.requireTime = multiple * round * 120;
      this.endTime = moment().add(this.requireTime, 'm').format('YYYY-MM-DD HH:mm:ss')
      this.showCalc = true;
    },
    handleProduce(v) {
      const id = v.id;
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "请选择生产物品"})
