            to: 'dfsmetaverse',
            quantity: elem,
            memo: 'deposit',
          }          
        });
      })
      params.actions.push({
        account: 'dfsmetaverse',
        name: 'produce2',
        authorization: [{
          actor: name,
          permission,
        }],
        data: {
          assetid: id,
          multiple: multiple,
          round: round,
          user: name,
          workid: workid,
        }
      });
      DApp.toTransaction(params, (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleProduce tx error");
          this.logMsg.push(err.message);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleProduce tx error");
          this.logMsg.push(err.message);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        if (!v.autoSwitch) {
          setTimeout(this.handleUpdateTask, this.updateDelay, v);
        }
      })      
