    handleFinish(v) {
      if (v.task.exist) {
        const mature_time = moment(v.task.mature_time);
        const countDown = mature_time.diff(moment());
        if (countDown > 0) {
          v.task.countDown = countDown;
          this.$refs['#'+v.id][0].start();
          console.log("start")
          return;
        }
      }
      if (v.autoSwitch) {
        if (v.task.exist) {
          setTimeout(this.handleAutoHarvest, this.delay, v);
        } else {
          if (v.workid === 'RUBY' || v.workid === 'BERYL' || v.workid === 'TOPAZ') {
            setTimeout(this.handleAutoMakeGem, this.delay, v);
          } else {
            setTimeout(this.handleAutoProduce, this.delay, v);
          }
        }
      }
    },
