      if (workid === "RUBY") {
        quantity = accDiv(multiple*round, this.price[0]).toFixed(4) + ' ' + 'USDC'
        gem = multiple.toFixed(4) + ' ' + workid;
        contract = 'usdxusdxusdx'
      } else if (workid === "BERYL") {
        quantity = accDiv(multiple*round, this.price[1]).toFixed(4) + ' ' + 'DFG'
        gem = multiple.toFixed(4) + ' ' + workid
        contract = 'dfxtokenmain'
      } else if (workid === "TOPAZ") {
        quantity = accDiv(multiple*round, this.price[2]).toFixed(8) + ' ' + 'TAG'
        gem = multiple.toFixed(4) + ' ' + workid
        contract = 'tagtokenmain'
      }

      const params = {
        actions: [{
          account: contract,
          name: 'transfer',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            from: name,
            to: 'dfsmetaverse',
            quantity: quantity,
            memo: 'deposit',
          }          
        },{
          account: 'dfsmetaverse',
          name: 'makegem2',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            user: name,
            assetid: id,
            gem: gem,
            round: round,
          }          
        }]
      }
      DApp.toTransaction(params, async (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoMakeGem tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoMakeGem);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoMakeGem tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoMakeGem);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        v.failCount = 0;
        await this.handleUpdateTask(v);
        this.handleAutoMakeGem(v);
      }) 
    },
    async handleFailProcess(v, func) {
      this.handleUpdateTask(v); //这里不需要await，因为setTimeout有延时了
      v.failCount++;
      if (v.failCount > this.failMax) {
        this.logMsg.push("超过失败重试最大次数");
        v.failCount = 0;
        v.autoSwitch = false;
      } else {
        if (v.autoSwitch == false) {
          v.failCount = 0;
          this.logMsg.push("已退出自动模式");
        } else {
          this.logMsg.push("失败重试第"+v.failCount+"次");
          setTimeout(func, this.failDelay, v);
        }
      }
    },
    handleSwitchClick(v) {
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "请选择生产物品",})
        this.logMsg.push("请选择生产物品");
        return;        
      }
    },
    handleSwitchCheck(v) {
      if (v.autoSwitch) {
        this.$refs['#'+v.id][0].start();
        console.log("start")
      }
    },
    handleFreeCPU() {
      this.$store.dispatch('setFreeCpu', this.freeCPU);
    },
    noSleep() {
    let noSleep = new NoSleep();
      document.addEventListener('click',
        function enableNoSleep () {
          noSleep.enable();
          document.removeEventListener('click', enableNoSleep, false);
        },
        false);      
    }
  },
   mounted() {
    this.noSleep();
	  this.handleLogin();
    this.handleFreeCPU();
    this.handleUpdateTip();
    this.handleGetPrice();
  },
}
</script>

